import React from 'react'
import '../App.css'
import './JanaceBlogMiddle.css'

function JanaceBlogMiddle(props) {
  return (
    <>
      <div className='offwhite-background'>
        <div className='janace-blog-middle-container'>
          <p>Updated on 9.9.2024 by Fernanda Ortiz</p><br/><br/>
          <p>Janace Griffinbetter known as Junkdredzis a creative force whos done it all and still manages to 
            keep us guessing about what shell tackle next. A published childrens book author, founder of The 
            El Paso Black Arts Association, and all-around artistic powerhouse, Janace doesnt just stop there. 
            Shes also out here chasing her dream of being a fashion designerand absolutely crushing it.<br/><br/>
            From major publications to iconic events, Janaces designs have been showcased on platforms we all know: 
            KVIA, KTSM, PBS, The City Magazine, the UTEP Prospector, MagsBlack Magazine, Vigour Magazineyou name it! 
            Add appearances at The Crossland Art Gallery, Audi Fashion Week, and L.A. Fashion Week, and were talking 
            serious trailblazing. Oh, and did we mention The El Paso Strong Fashion Show? Yeah, she created that. 
            For free. Every. Single. Year.
          </p>
          <div className='janace-images-container'>
              <img src='../images/janace-2.jpg' alt='Janace Image'></img>
          </div>
          <div className='janace-body-container'>
            <div className='janace-left-column'>
              <p><b><span>PAINTED LADIES: A PROUD MOMENT</span></b><br/>
                With four stunning collections in the books, Janace isnt short on proud accomplishments. 
                But one that truly holds a special place in her heart? The Painted Ladies Collection. 
                This one went deeptapping into the emotional turmoil of toxic relationships and domestic 
                violence survivors.<br/><br/>
                In her own words:<br/><br/>
                  <i>The whole thought behind it was throwing the paint and slashing the dress 
                  because its almost like what I feel like women can go through during relationships 
                  like that.</i><br/><br/>
                It wasnt just fashionit was healing, empowerment, and art combined.
              </p>
              <br/>
              <p><b><span>DEATH BY DENIM: A JOURNEY OF TRANSFORMATION</span></b><br/>
                Janaces Death by Denim Fashion Show hits different. This collection is personala reflection 
                of her life over the past few transformative years. From the heartbreaking loss of her father 
                in 2018 to marriage, parenthood, career changes, and shifting friendships, Janaces life has 
                been a rollercoaster of endings and beginnings.<br/><br/>
                The shows concept is powerful: It symbolizes lifes constant cycle of death and rebirth. Each day, 
                were given the opportunity to wake up as new people, ready to take on fresh goals, passions, or 
                dreams.<br/><br/>
                And what better material to capture this idea than <strong>denim</strong>?
              </p>
              <br/>
              <p><b><span>WHY DENIM?</span></b><br/>
                Denim is the ultimate metaphor for reinvention. Like denim, our experiencesno matter how ripped, 
                worn, or beaten downcan become badges of honor. Janace reminds us that we are not defined by our 
                struggles but refined by them.<br/><br/>
                Whether its losing a job, hitting a low point, or weathering change, Death by Denim challenges 
                us to upcycle and recycle not just fabrics, but our lives. To take what weve overcome and emerge 
                <strong> stronger</strong>, <strong>bolder</strong>, and <strong>better</strong>.
              </p>
              <br/>
              <p><b><span>FOLLOW YOUR DREAMS, NO MATTER WHAT</span></b><br/>
                If youre looking for motivation to chase your own goals, Janace has some advice:<br/><br/>
                  <i>I believe in following your dreams and never giving up, no matter where you are or who you 
                    know. It doesnt even matter if you have a solid plan just start moving forward, and the 
                    direction will reveal itself as you go.</i><br/><br/>
                Whether you want to snap photos, design clothes, or write that bestseller, grab that camera, 
                dust off the sewing machine, upgrade that laptop. Start now. Invest in yourself. And let 
                the journey unfold.
              </p>
              <br/>
              <p><strong>
                Janace Griffin is not just paving the way for Black artistsshes showing us all that no dream 
                is too big and no transformation is too bold.
              </strong></p>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default JanaceBlogMiddle